*{
  box-sizing: border-box;
}
h1, h2, h3, h4, h5, h6{
  margin: 0;
}

body{
  background-color: #f2f2f2;
}

.container{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  margin-top: 50px;
  margin-bottom: 100px;
  max-width: 1000px;
}

.films{
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0 5px;
}

.film-card{
  max-width: 450px;
  padding: 10px;
  margin-bottom: 70px;
  background-color: #fff;
  border-radius: 5px;
  padding-bottom: 25px;
  display: flex;
  color: #adaeb9;  
  box-shadow: 0 0 38px -9px rgb(34 60 80 / 14%);
  transition: box-shadow .5s;
}
.film-card:hover{
  cursor: pointer;
  box-shadow: 0 0 38px -9px rgb(34 60 80 / 30%);  
}

.film-card img{
  max-width: 170px;
  max-height: 250px;
  height: 100%;
  position: relative;
  top: -40px;
  margin-right: 20px;
}

.movie-column{
  font-size: 14px;
  font-weight: 300;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.film-card__title{
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 24px;
  color: #2c2c2c;
}

.film-card__year{
  margin-right: 10px;
  font-size: 14px;
  margin-bottom: 5px;
}

.film-card__genre{
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 14px;
  margin-bottom: 10px;
}
.film-card__genre li{
 display: inline-block;
 margin-right: 10px;
}